import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component:resolve => require(['@/pages/main'],resolve),
    },
    {
      path: '/about',
      name: 'about',
      component:resolve => require(['@/pages/onepage/about'],resolve),
    },
    {
      path: '/newsList',
      name: 'newsList',
      component:resolve => require(['@/pages/news/newsList'],resolve),
    },
    {
      path: '/newsMsg',
      name: 'newsMsg',
      component:resolve => require(['@/pages/news/newsMsg'],resolve),
    },
    {
      path: '/proList',
      name: 'proList',
      component:resolve => require(['@/pages/produce/proList'],resolve),
    },
    {
      path: '/proMsg',
      name: 'proMsg',
      component:resolve => require(['@/pages/produce/proMsg'],resolve),
    },
    {
      path: '/contact',
      name: 'contact',
      component:resolve => require(['@/pages/onepage/contact'],resolve),
    },
    {
      path: '/team',
      name: 'team',
      component:resolve => require(['@/pages/onepage/team'],resolve),
    }
  ]
})



